import { NextRequest, NextResponse } from 'next/server';
import { verifyAuth } from '@/lib/auth';
import databaseAdapter from '@/lib/database-adapter-optimized';

// –ü–æ–ª—É—á–µ–Ω–∏–µ —Å–ø–∏—Å–∫–∞ –ø—Ä–æ–µ–∫—Ç–æ–≤
export async function GET(request: NextRequest) {
  try {
    const authResult = await verifyAuth(request);
    if (!authResult.success) {
      return NextResponse.json(
        { error: authResult.error },
        { status: 401 }
      );
    }

    const { userId, role } = authResult.user!;

    let projects;
    if (role === 'admin') {
      // –ê–¥–º–∏–Ω—ã –≤–∏–¥—è—Ç –≤—Å–µ –ø—Ä–æ–µ–∫—Ç—ã
      projects = await databaseAdapter.getAllProjects();
    } else {
      // –û–±—ã—á–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–∏ –≤–∏–¥—è—Ç —Ç–æ–ª—å–∫–æ —Å–≤–æ–∏ –ø—Ä–æ–µ–∫—Ç—ã
      projects = await databaseAdapter.getProjectsByUserId(Number(userId));
    }

    // –ü—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–∏–µ –≤ —Ñ–æ—Ä–º–∞—Ç API —Å –ø—Ä–∞–≤–∏–ª—å–Ω–æ–π —Ç–∏–ø–∏–∑–∞—Ü–∏–µ–π
    const projectsResult = projects.map(project => ({
      id: project.id, // –û—Å—Ç–∞–≤–ª—è–µ–º ID –∫–∞–∫ —Å—Ç—Ä–æ–∫—É –¥–ª—è —Å–æ–≤–º–µ—Å—Ç–∏–º–æ—Å—Ç–∏ —Å API —É–¥–∞–ª–µ–Ω–∏—è
      name: project.name,
      description: project.description || null,
      status: project.status || 'active',
      color: project.color || '#3B82F6',
      createdAt: project.createdAt,
      updatedAt: project.updatedAt,
      userId: Number(project.createdBy) // –ò—Å–ø–æ–ª—å–∑—É–µ–º createdBy –∏–∑ —Å—Ö–µ–º—ã –ë–î
    }));

    return NextResponse.json({ projects: projectsResult });
  } catch (error) {
    console.error('Projects API error:', error);
    return NextResponse.json(
      { error: '–í–Ω—É—Ç—Ä–µ–Ω–Ω—è—è –æ—à–∏–±–∫–∞ —Å–µ—Ä–≤–µ—Ä–∞' },
      { status: 500 }
    );
  }
}

// –°–æ–∑–¥–∞–Ω–∏–µ –Ω–æ–≤–æ–≥–æ –ø—Ä–æ–µ–∫—Ç–∞
export async function POST(request: NextRequest) {
  try {
    const authResult = await verifyAuth(request);
    if (!authResult.success) {
      return NextResponse.json(
        { error: authResult.error },
        { status: 401 }
      );
    }

    const { userId } = authResult.user!;
    const { name, description, color, icon } = await request.json();

    if (!name) {
      return NextResponse.json(
        { error: '–ù–∞–∑–≤–∞–Ω–∏–µ –ø—Ä–æ–µ–∫—Ç–∞ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ' },
        { status: 400 }
      );
    }
    
    if (!userId) {
      return NextResponse.json(
        { error: '–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω' },
        { status: 401 }
      );
    }

    const projectData = {
      name,
      description: description || null,
      createdBy: userId, // userId —É–∂–µ —è–≤–ª—è–µ—Ç—Å—è UUID —Å—Ç—Ä–æ–∫–æ–π
      color: color || '#3B82F6'
    };
    const project = await databaseAdapter.createProject(projectData);
    
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º, —á—Ç–æ –ø—Ä–æ–µ–∫—Ç –±—ã–ª —Å–æ–∑–¥–∞–Ω —É—Å–ø–µ—à–Ω–æ
    if (!project || !project.id) {
      console.error('Failed to create project - no project ID returned');
      return NextResponse.json(
        { error: '–û—à–∏–±–∫–∞ —Å–æ–∑–¥–∞–Ω–∏—è –ø—Ä–æ–µ–∫—Ç–∞' },
        { status: 500 }
      );
    }

    // –°–æ–∑–¥–∞–µ–º –¥–æ—Å–∫—É –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é –¥–ª—è –Ω–æ–≤–æ–≥–æ –ø—Ä–æ–µ–∫—Ç–∞
    const board = await databaseAdapter.createBoard(
      `–î–æ—Å–∫–∞ –ø—Ä–æ–µ–∫—Ç–∞ ${name}`,
      '', // description –Ω–µ –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –≤ —Å—Ö–µ–º–µ
      project.id
    );
    
    console.log('Created board:', board);
    console.log('Board ID:', board.id, 'Type:', typeof board.id);

    // –°–æ–∑–¥–∞–µ–º –∫–æ–ª–æ–Ω–∫–∏ –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é
    const defaultColumns = [
      { name: '–ö –≤—ã–ø–æ–ª–Ω–µ–Ω–∏—é', color: '#ef4444', position: 0 },
      { name: '–í —Ä–∞–±–æ—Ç–µ', color: '#f59e0b', position: 1 },
      { name: '–ù–∞ –ø—Ä–æ–≤–µ—Ä–∫–µ', color: '#3b82f6', position: 2 },
      { name: '–í—ã–ø–æ–ª–Ω–µ–Ω–æ', color: '#10b981', position: 3 }
    ];

    for (const column of defaultColumns) {
      await databaseAdapter.createColumn(
        column.name,
        board.id,
        column.position,
        column.color
      );
    }

    // –ü—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–∏–µ –≤ —Ñ–æ—Ä–º–∞—Ç API —Å –ø—Ä–∞–≤–∏–ª—å–Ω–æ–π —Ç–∏–ø–∏–∑–∞—Ü–∏–µ–π
    const projectResult = {
      id: project.id,
      name: project.name,
      description: project.description || null,
      status: project.status || 'active',
      color: project.color || '#3B82F6',
      createdAt: project.createdAt,
      updatedAt: project.updatedAt,
      userId: project.createdBy
    };

    return NextResponse.json(
      { project: projectResult },
      { status: 201 }
    );

  } catch (error) {
    console.error('–û—à–∏–±–∫–∞ —Å–æ–∑–¥–∞–Ω–∏—è –ø—Ä–æ–µ–∫—Ç–∞:', error);
    return NextResponse.json(
      { error: '–í–Ω—É—Ç—Ä–µ–Ω–Ω—è—è –æ—à–∏–±–∫–∞ —Å–µ—Ä–≤–µ—Ä–∞' },
      { status: 500 }
    );
  }
}

// –£–¥–∞–ª–µ–Ω–∏–µ –ø—Ä–æ–µ–∫—Ç–∞ (—Ç–æ–ª—å–∫–æ –¥–ª—è –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–æ–≤)
export async function DELETE(request: NextRequest) {
  try {
    const authResult = await verifyAuth(request);
    if (!authResult.success) {
      return NextResponse.json(
        { error: authResult.error },
        { status: 401 }
      );
    }

    const { user } = authResult;
    
    // –ü—Ä–æ–≤–µ—Ä–∫–∞ –ø—Ä–∞–≤ –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–∞
    if (user.role !== 'admin') {
      return NextResponse.json(
        { error: '–ù–µ–¥–æ—Å—Ç–∞—Ç–æ—á–Ω–æ –ø—Ä–∞–≤ –¥–ª—è —É–¥–∞–ª–µ–Ω–∏—è –ø—Ä–æ–µ–∫—Ç–∞' },
        { status: 403 }
      );
    }

    const { searchParams } = new URL(request.url);
    const projectId = searchParams.get('projectId');

    if (!projectId) {
      return NextResponse.json(
        { error: 'ID –ø—Ä–æ–µ–∫—Ç–∞ –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω' },
        { status: 400 }
      );
    }

    // –ü—Ä–æ–≤–µ—Ä–∫–∞ —Å—É—â–µ—Å—Ç–≤–æ–≤–∞–Ω–∏—è –ø—Ä–æ–µ–∫—Ç–∞
    const project = await databaseAdapter.getProjectById(projectId);
    if (!project) {
      return NextResponse.json(
        { error: '–ü—Ä–æ–µ–∫—Ç –Ω–µ –Ω–∞–π–¥–µ–Ω' },
        { status: 404 }
      );
    }

    // –£–¥–∞–ª–µ–Ω–∏–µ –ø—Ä–æ–µ–∫—Ç–∞
    await databaseAdapter.deleteProject(projectId);

    return NextResponse.json(
      { message: '–ü—Ä–æ–µ–∫—Ç —É—Å–ø–µ—à–Ω–æ —É–¥–∞–ª–µ–Ω' },
      { status: 200 }
    );

  } catch (error) {
    console.error('–û—à–∏–±–∫–∞ —É–¥–∞–ª–µ–Ω–∏—è –ø—Ä–æ–µ–∫—Ç–∞:', error);
    return NextResponse.json(
      { error: '–í–Ω—É—Ç—Ä–µ–Ω–Ω—è—è –æ—à–∏–±–∫–∞ —Å–µ—Ä–≤–µ—Ä–∞' },
      { status: 500 }
    );
  }
}